export default {
	name: "picker-region",
	props: ['dataList','title'],
	data: function() {
		return {
			// 记录当前索引
			recordList: [],
			status: false,
			value: [0,0,0]
		}
	},

	methods: {
		bindChange(e) {
			this.recordList = e.detail.value;
		},
		
		justClose() {
			this.status = true;
			this.$emit('showStatus', this.status)
		},
		
		reSelected() {
			this.status = true
			this.$emit('showStatus', this.status)
			this.$emit('listenChild', this.recordList)
		}
	}
}
